@use "../base/colors" as *;
@use "../base/mixins" as *;

.quote {
	&__box {
		text-align: center;
		padding: 0 4em;
		&-text {
			font-size: clamp(2rem, 1.325rem + 1.875vw, 3.2rem);
			color: #fff;
			font-weight: 400;
		}
		&-author {
			margin: 1em auto;
			font-weight: 700;
			font-size: clamp(2.2rem, 1.1875rem + 2.8125vw, 4rem);
			position: relative;
            color: #fff;
            width: max-content;
			
		}
	}
}
.section {
	&-quote {
		padding: 10em 0;
		background-image: url("../../dist/img/quote_image.jpg");
		// background-attachment: fixed;
		background-size: cover;
		background-position: center;
		position: relative;
		width: 100%;
		z-index: 1;
		&::after {
			@include background-shadow(0.3);
		}
	}
}
