@use "./base/mixins" as *;
@use "./base/colors" as *;

.services {
	display: grid;
	grid-template-columns: repeat(1, 1fr);
	margin-top: 10em;
	gap: 4em;

	&__item {
		display: flex;
		flex-direction: column;
		gap: 3em;
		// border: 1px solid hsl(0, 0%, 82%);
		border-radius: 8px;
		text-align: left;
		box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Prosty box-shadow */
		padding: 4em 4em;
		opacity: 0;
		&.animate {
			@include fade-in-left;

			animation-delay: 0.1s;
		}
		// &:nth-child(1), &:nth-child(4){
		//     border-right: 1px solid #272727;
		// }
		// &:nth-child(2), &:nth-child(5){
		//     border-right: 1px solid #272727;
		//     border-left: 1px solid #272727;

		// }
		// &:nth-child(3), &:nth-child(6){
		//     border-left: 1px solid #272727;
		// }
		&-icon {
			height: 50px;
			width: 50px;
			padding: 1em;
			background-color: $main-color;
			border-radius: 4px;
		}
		&-heading {
			font-size: clamp(1.8rem, 1.575rem + 0.625vw, 2.2rem);
			color: #000;
			letter-spacing: 1.2px;
			font-weight: 600;
			-webkit-text-stroke: 0.2px black; /* Obramowanie tekstu */
			text-stroke: 0.2px black;
		}
		&-text {
			font-weight: 400;
			font-size: clamp(1.6rem, 1.4875rem + 0.3125vw, 1.8rem);
			color: #191919;
		}
	}
}
.section-services {
	background-color: hsl(0, 0%, 96%);
}
