@use "../base/colors" as *;
@use "../base/mixins" as *;

.section-process {
	background-color: #fff;
}
.process {
	display: grid;
	grid-template-columns: repeat(1, 1fr);
	gap: 6em;
	margin-top: 10em;

	&__item {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		gap: 4em;
        padding:8em 2.5em;
		box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Prosty box-shadow */
		text-align: center;
		color: #fff;
		// background-color: #0F0F0F;
		border-radius: 8px;
		z-index: 1;
		opacity: 0;
		width: 100%;
		&.animate {
			@include fade-in-bottom;

				animation-delay: 0.1s;
		
		}
		&::after {
			display: none;
			position: absolute;
			top: -20px;
			right: -20px;
			height: 60px;
			width: 60px;
			border-radius: 50%;
			background-color: $main-color;
			font-size: clamp(3rem, 2.4375rem + 1.5625vw, 4rem);
			line-height: 60px;
			color: #fff;
			z-index: 231;
			text-align: center;
		}
		&:nth-child(1) {
			&::after {
				content: "01";
				font-weight: 300;
			}
		}
		&:nth-child(2) {
			&::after {
				content: "02";
				font-weight: 500;
			}
		}
		&:nth-child(3) {
			&::after {
				content: "03";
				font-weight: 700;
			}
		}

		& span {
			position: relative;
			display: flex;
			gap: 2em;
			align-items: center;
			padding: 1em 0;
            & img{
                height: 60px;
				width: auto;
				background-color: $main-color;
				padding: 2em;
				border-radius: 4px;
            }
			&::after {
				position: absolute;
				bottom: -10px;
				left: 0;
				width: 100%;
				height: 2px;
				background-color: $main-color;
				z-index: 1;
			}
		}
		&-heading {
			text-transform: uppercase;
			color: $main-color;
            font-weight: 600;
			font-size: clamp(2rem, 1.775rem + 0.625vw, 2.4rem);
		}
		&-text {
			color: #000;
			font-weight: 600;
			font-size: clamp(1.8rem, 1.6875rem + 0.3125vw, 2rem);
		}
		&-description {
			font-size: clamp(1.6rem, 1.4875rem + 0.3125vw, 1.8rem);
			color: #373737;
		}
	}
	&__button {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		max-width: 200px;
		background-color: $main-color;
		color: #fff;
		font-size: clamp(1.6rem, 1.375rem + 0.625vw, 2rem);
		border-radius: 8px;
		padding: 0.6em 2em;
		margin: 2em auto;
		opacity: 0;
		transition: color 0.3s, background-color 0.3s;
		&:hover {
			background-color: $hover-color;
		}
		&.animate {
			@include fade-in-bottom;

				animation-delay: 0.1s;
		
		}
	}
	
}
