* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Montserrat", sans-serif;
}

body {
  width: 100%;
  height: 100%;
  overflow-x: clip;
}

body,
html {
  scroll-behavior: smooth;
  scroll-padding-top: 80px;
  font-size: 62.5%;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1400px;
  padding: 0em 4em;
}

a {
  text-decoration: none;
  color: #000;
}

ul {
  list-style-type: none;
}

.header {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80dvh;
  width: 100%;
  background-image: url("./dist/img/header_small.jpg");
  background-size: cover;
  background-position: center;
  z-index: 12;
}
.header::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: -2;
}
.header::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(68, 57, 168, 0) 41%, rgb(12, 12, 12) 98%);
  z-index: -1;
}
.header__box {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6em;
}
.header__box-heading {
  text-align: center;
  font-weight: 500;
  font-size: clamp(3rem, 1.5375rem + 4.0625vw, 5.6rem);
  color: #fff;
}
.header__buttons {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3em;
  width: 100%;
}
.header__buttons-button {
  width: 100%;
  max-width: 320px;
  padding: 0.6em 2em;
  border-radius: 8px;
  font-size: clamp(1.6rem, 1.15rem + 1.25vw, 2.4rem);
  font-weight: 500;
  text-align: center;
}
.header__buttons-button--first {
  background-color: hsl(246, 84%, 60%);
  border: 2px solid hsl(246, 84%, 60%);
  color: #fff;
  transition: color 0.3s, background-color 0.3s, border 0.3s;
}
.header__buttons-button--first:hover {
  background-color: hsl(246, 84%, 56%);
  border: 2px solid hsl(246, 84%, 56%);
}
.header__buttons-button--second {
  transition: color 0.3s, border 0.3s;
  border: 2px solid #fff;
  color: #fff;
}
.header__buttons-button--second:hover {
  color: hsl(246, 84%, 60%);
  border: 2px solid hsl(246, 84%, 60%);
}

.socialmedia__list {
  display: flex;
  gap: 2em;
}
.socialmedia__list-icon {
  height: 35px;
  width: auto;
}

.navbar {
  position: fixed;
  top: 0;
  z-index: 123123;
  width: 100%;
  transition: background-color 0.3s;
}
.navbar__link {
  color: #fff;
  font-size: clamp(2rem, 1.2135rem + 4.4944vw, 4rem);
}
.navbar-shadow {
  background-color: hsl(0, 0%, 5%);
}
.navbar__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3em 0;
  width: 100%;
  height: 60px;
}
.navbar__items {
  display: flex;
  gap: 4em;
  padding: 6em 4em;
}
.navbar__items.menu-active {
  display: flex;
  transform: translateX(0);
}
.navbar__logo {
  font-size: clamp(2.4rem, 2.175rem + 0.625vw, 2.8rem);
  color: #fff;
  font-weight: 600;
}
.navbar__socialmedia-mobile {
  margin-top: auto;
  border-top: 1px solid #fff;
  padding: 5em 0;
}
.navbar__socialmedia-mobile .socialmedia__list {
  flex-direction: row;
  gap: 4em;
}
.navbar__socialmedia-mobile .socialmedia__list-icon {
  height: 50px;
}
.navbar__socialmedia-desktop {
  display: none;
}
.navbar__socialmedia-desktop .socialmedia__list-item:nth-child(2) {
  border-left: 2px solid #8c8c8c;
  border-right: 2px solid #8c8c8c;
  padding: 0 2em;
}
.navbar__socialmedia-item:nth-child(2) {
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
  padding: 0 2em;
}

.line {
  margin: 0.3em;
  height: 0.2em;
  background-color: #fff;
  border-radius: 20px;
  transition: background-color 0.3s, width 0.3s;
}
.line:nth-child(1) {
  width: 1.4em;
}
.line:nth-child(2) {
  width: 3em;
}
.line:nth-child(3) {
  width: 1.2em;
  margin-left: auto;
}

.burger {
  display: flex;
  flex-direction: column;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}
.burger:focus .line {
  width: 3em;
  background-color: hsl(246, 84%, 60%);
}

.services {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  margin-top: 10em;
  gap: 4em;
}
.services__item {
  display: flex;
  flex-direction: column;
  gap: 3em;
  border-radius: 8px;
  text-align: left;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Prosty box-shadow */
  padding: 4em 4em;
  opacity: 0;
}
.services__item.animate {
  animation: fade-in-left 1s forwards;
  animation-delay: 0.1s;
}
@keyframes fade-in-left {
  0% {
    opacity: 0;
    transform: translateX(-300px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
.services__item-icon {
  height: 50px;
  width: 50px;
  padding: 1em;
  background-color: hsl(246, 84%, 60%);
  border-radius: 4px;
}
.services__item-heading {
  font-size: clamp(1.8rem, 1.575rem + 0.625vw, 2.2rem);
  color: #000;
  letter-spacing: 1.2px;
  font-weight: 600;
  -webkit-text-stroke: 0.2px black; /* Obramowanie tekstu */
  text-stroke: 0.2px black;
}
.services__item-text {
  font-weight: 400;
  font-size: clamp(1.6rem, 1.4875rem + 0.3125vw, 1.8rem);
  color: #191919;
}

.section-services {
  background-color: hsl(0, 0%, 96%);
}

.section-about-us {
  overflow: hidden;
  position: relative;
  z-index: 1;
}
.section-about-us .section__box-text {
  color: #fff;
}

.about-us {
  margin-top: 12em;
}
.about-us__item {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 4em 0;
  max-width: 1100px;
  margin: 0 auto;
  opacity: 0;
}
.about-us__item:nth-child(1).animate {
  animation: fade-in-right 1s forwards;
  animation-delay: 0.1s;
}
@keyframes fade-in-right {
  0% {
    opacity: 0;
    transform: translateX(300px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
.about-us__item:nth-child(1) .about-us__text-heading {
  border: 2px solid #d74343;
  color: #d74343;
}
.about-us__item:nth-child(2) {
  opacity: 1;
  position: relative;
  z-index: 1;
}
.about-us__item:nth-child(2)::after {
  position: absolute;
  content: "";
  width: 100vw;
  height: 100%;
  background-color: #100f0f;
  z-index: -1;
}
.about-us__item:nth-child(2) .about-us__text-heading {
  border: 2px solid hsl(246, 84%, 60%);
  color: hsl(246, 84%, 60%);
}
.about-us__item:nth-child(3).animate {
  animation: fade-in-left 1s forwards;
  animation-delay: 0.1s;
}
@keyframes fade-in-left {
  0% {
    opacity: 0;
    transform: translateX(-300px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
.about-us__item:nth-child(3) .about-us__text-heading {
  border: 2px solid #46d743;
  color: #46d743;
}
.about-us__text {
  text-align: center;
}
.about-us__text-heading {
  margin: 0 auto;
  font-size: clamp(1.6rem, 1.2625rem + 0.9375vw, 2.2rem);
  border-radius: 12px;
  font-weight: 600;
  padding: 0.3em 0.6em;
  width: max-content;
}
.about-us__text-text {
  font-size: clamp(2rem, 0.875rem + 3.125vw, 4rem);
  color: #fff;
  font-weight: 600;
  margin: 0.5em 0;
}
.about-us__text-description {
  font-size: clamp(1.6rem, 1.2625rem + 0.9375vw, 2.2rem);
  color: #a8a8a8;
}
.about-us__image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.section-process {
  background-color: #fff;
}

.process {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 6em;
  margin-top: 10em;
}
.process__item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 4em;
  padding: 8em 2.5em;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Prosty box-shadow */
  text-align: center;
  color: #fff;
  border-radius: 8px;
  z-index: 1;
  opacity: 0;
  width: 100%;
}
.process__item.animate {
  animation: fade-in-bottom 1s forwards;
  animation-delay: 0.1s;
}
@keyframes fade-in-bottom {
  0% {
    opacity: 0;
    transform: translateY(300px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.process__item::after {
  display: none;
  position: absolute;
  top: -20px;
  right: -20px;
  height: 60px;
  width: 60px;
  border-radius: 50%;
  background-color: hsl(246, 84%, 60%);
  font-size: clamp(3rem, 2.4375rem + 1.5625vw, 4rem);
  line-height: 60px;
  color: #fff;
  z-index: 231;
  text-align: center;
}
.process__item:nth-child(1)::after {
  content: "01";
  font-weight: 300;
}
.process__item:nth-child(2)::after {
  content: "02";
  font-weight: 500;
}
.process__item:nth-child(3)::after {
  content: "03";
  font-weight: 700;
}
.process__item span {
  position: relative;
  display: flex;
  gap: 2em;
  align-items: center;
  padding: 1em 0;
}
.process__item span img {
  height: 60px;
  width: auto;
  background-color: hsl(246, 84%, 60%);
  padding: 2em;
  border-radius: 4px;
}
.process__item span::after {
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: hsl(246, 84%, 60%);
  z-index: 1;
}
.process__item-heading {
  text-transform: uppercase;
  color: hsl(246, 84%, 60%);
  font-weight: 600;
  font-size: clamp(2rem, 1.775rem + 0.625vw, 2.4rem);
}
.process__item-text {
  color: #000;
  font-weight: 600;
  font-size: clamp(1.8rem, 1.6875rem + 0.3125vw, 2rem);
}
.process__item-description {
  font-size: clamp(1.6rem, 1.4875rem + 0.3125vw, 1.8rem);
  color: #373737;
}
.process__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 200px;
  background-color: hsl(246, 84%, 60%);
  color: #fff;
  font-size: clamp(1.6rem, 1.375rem + 0.625vw, 2rem);
  border-radius: 8px;
  padding: 0.6em 2em;
  margin: 2em auto;
  opacity: 0;
  transition: color 0.3s, background-color 0.3s;
}
.process__button:hover {
  background-color: hsl(246, 84%, 56%);
}
.process__button.animate {
  animation: fade-in-bottom 1s forwards;
  animation-delay: 0.1s;
}
@keyframes fade-in-bottom {
  0% {
    opacity: 0;
    transform: translateY(300px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.section-faq {
  background-color: #fff;
  padding: 20em 0;
}

.accordion {
  margin: 10em auto;
  width: 100%;
  max-width: 900px;
}
.accordion__box {
  margin: 2em 0;
  border-bottom: 1px solid #949494;
}
.accordion__box .plus {
  font-size: 5rem;
  font-weight: 300;
}
.accordion__box-btn {
  font-weight: 400;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5em 0em;
  width: 100%;
  border: none;
  background-color: #fff;
  color: #000;
  font-size: clamp(1.6rem, 1.375rem + 0.625vw, 2rem);
  text-align: left;
  border-radius: 20px;
  cursor: pointer;
}
.accordion__info {
  color: #282828;
  font-size: clamp(1.6rem, 1.375rem + 0.625vw, 2rem);
  transition: max-height 0.6s ease;
  pointer-events: none;
  max-height: 0;
  overflow: hidden;
}
.accordion__info.active {
  max-height: 350px;
}
.accordion__info-text {
  padding: 1em 1em;
  font-size: clamp(1.6rem, 1.4875rem + 0.3125vw, 1.8rem);
}

.quote__box {
  text-align: center;
  padding: 0 4em;
}
.quote__box-text {
  font-size: clamp(2rem, 1.325rem + 1.875vw, 3.2rem);
  color: #fff;
  font-weight: 400;
}
.quote__box-author {
  margin: 1em auto;
  font-weight: 700;
  font-size: clamp(2.2rem, 1.1875rem + 2.8125vw, 4rem);
  position: relative;
  color: #fff;
  width: max-content;
}

.section-quote {
  padding: 10em 0;
  background-image: url("../../dist/img/quote_image.jpg");
  background-size: cover;
  background-position: center;
  position: relative;
  width: 100%;
  z-index: 1;
}
.section-quote::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: -2;
}

.section-contact {
  background-image: url("../../../dist/img/contact_big.jpg");
  background-size: cover;
  background-position: center;
  position: relative;
  padding: 10em 0;
  z-index: 1;
}
.section-contact::before {
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(68, 57, 168, 0) 41%, rgb(12, 12, 12) 98%);
  z-index: -1;
  bottom: 0;
  left: 0;
}
.section-contact .section__box-text {
  color: #fff;
}

.contact__box {
  margin-top: 10em;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact__box-email {
  display: flex;
  align-items: center;
  gap: 4em;
}
.contact__box-email img {
  height: 60px;
}
.contact__box-email a {
  color: #fff;
  font-size: clamp(2.4rem, 1.95rem + 1.25vw, 3.2rem);
  word-break: break-all;
}
.contact__socialmedia {
  display: flex;
  gap: 4em;
  margin: 5em 0;
  justify-content: space-evenly;
}
.contact__socialmedia-item a img {
  height: 40px;
}

.footer {
  height: 80px;
  line-height: 80px;
  text-align: center;
  color: hsl(0, 0%, 55%);
  background-color: hsl(0, 3%, 8%);
  font-size: clamp(1.6rem, 1.375rem + 0.625vw, 2rem);
  border-top: 2px solid #3a3a3a;
}
.footer .gradient-text {
  font-weight: 600;
}
.footer-logo {
  color: #fff;
  font-weight: 600;
}

body {
  background-color: #121111;
}

.section-padding {
  padding: 6em 0;
}
.section__box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 2em;
  text-transform: uppercase;
}
.section__box-heading {
  font-size: clamp(1.6rem, 1.15rem + 1.25vw, 2.4rem);
  letter-spacing: 2px;
}
.section__box-text {
  font-size: clamp(2.4rem, 1.5rem + 2.5vw, 4rem);
  font-weight: 600;
  color: #000;
}

.no-scroll {
  overflow: hidden;
}

.gradient-text {
  text-transform: uppercase;
  color: hsl(246, 84%, 60%);
}

.arrow-to-top {
  position: fixed;
  display: block;
  opacity: 0;
  bottom: 20px;
  right: 20px;
  height: 30px;
  width: 30px;
  padding: 1em;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 14px;
  border: 2px solid transparent;
  box-sizing: content-box;
  transform: translateY(150%);
  cursor: pointer;
  pointer-events: none;
  transition: opacity 0.2s ease-in-out, transform 0.2s, border 0.2s ease-in-out, background-color 0.2s ease-in-out;
  z-index: 20130;
}
.arrow-to-top-active {
  transform: translateY(0);
  opacity: 1;
  pointer-events: auto;
}
.arrow-to-top img {
  height: 100%;
  width: auto;
  transform: rotate(180deg);
}
.arrow-to-top:hover {
  border: 2px solid hsl(246, 84%, 56%);
  background-color: hsl(246, 84%, 56%);
}

@media (min-width: 576px) {
  .header__buttons {
    flex-direction: row;
    width: 100%;
  }
  .header__buttons-button--second {
    width: max-content;
  }
  .navbar__items {
    padding: 6em 8em;
  }
  .container {
    padding: 0 8em;
  }
  .services__item {
    padding: 4em 6em;
  }
  .process__item {
    gap: 6em;
    padding: 8em 6em;
  }
}
@media (min-width: 768px) {
  .header {
    height: 75dvh;
  }
  .header__buttons {
    gap: 5em;
  }
  .services {
    grid-template-columns: repeat(2, 1fr);
  }
  .services__item-icon {
    height: 70px;
    width: 70px;
    padding: 2em;
  }
  .about-us__item {
    flex-direction: row;
  }
  .about-us__item:nth-child(2) {
    flex-direction: row-reverse;
  }
  .about-us__text, .about-us__image {
    flex: 1;
  }
  .about-us__text {
    justify-content: start;
    text-align: left;
    align-self: center;
  }
  .about-us__text-heading {
    margin: 0;
  }
  .process {
    grid-template-columns: repeat(2, 1fr);
  }
  .process__item::after {
    height: 80px;
    width: 80px;
    top: -20px;
    line-height: 80px;
    right: -20px;
  }
  .process__item span img {
    height: 80px;
  }
  .section-quote {
    padding: 20em 0;
  }
  .contact__box {
    flex-direction: row;
    justify-content: space-between;
    max-width: 900px;
    margin: 5em auto;
  }
  .arrow-to-top {
    height: 50px;
    width: 50px;
  }
}
@media (max-width: 991px) {
  .navbar__items {
    display: flex;
    position: absolute;
    flex-direction: column;
    justify-content: center;
    gap: 6em;
    height: calc(100dvh - 60px);
    width: 100%;
    background-color: #121111;
    left: 0;
    right: 0;
    top: 60px;
    bottom: 0;
    transition: transform 0.6s;
    transform: translateX(100%);
  }
}
@media (min-width: 992px) {
  .section-padding {
    padding: 12em 0;
  }
  .header {
    background-image: url("./dist/img/header_big.jpg");
  }
  .header__box {
    margin-bottom: 10em;
  }
  .navbar__content {
    height: 80px;
  }
  .navbar__links {
    display: flex;
  }
  .navbar__socialmedia-mobile {
    display: none;
  }
  .navbar__socialmedia-desktop {
    display: flex;
    flex-direction: row;
  }
  .navbar__items {
    display: flex;
    gap: 4em;
  }
  .navbar__items .navbar__link {
    font-size: 2rem;
  }
  .navbar .burger {
    display: none;
  }
  .services {
    grid-template-columns: repeat(3, 1fr);
  }
  .process {
    grid-template-columns: repeat(3, 1fr);
    gap: 2em;
  }
  .process__item {
    padding: 10em 6em;
  }
  .contact__box {
    margin: 10em auto 5em;
  }
  .contact__box-email img {
    height: 100px;
  }
  .contact__socialmedia-item a img {
    height: 50px;
  }
  .section-contact {
    padding: 15em 0;
  }
}
@media (min-width: 1200px) {
  .process {
    gap: 6em;
  }
}