@use "./base/colors" as *;
@use "./base/reset" as *;
@use "./layout/header" as *;
@use "./layout/navbar" as *;
@use "./abstracts/mixins" as *;
@use "./pages/services" as *;
@use "./pages/about" as *;
@use "./pages/process" as *;
@use "./pages/faq" as *;
@use "./pages/quote" as *;
@use "./pages/contact" as *;
@use "./layout/footer" as *;

body {
	background-color: $background-color;
}

.section {
	&-padding {
		padding: 6em 0;
	}
	&__box {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		text-align: center;
		gap: 2em;
		text-transform: uppercase;
		&-heading {
			font-size: clamp(1.6rem, 1.15rem + 1.25vw, 2.4rem);
			letter-spacing: 2px;
		}
		&-text {
			font-size: clamp(2.4rem, 1.5rem + 2.5vw, 4rem);
			font-weight: 600;
			color: #000;
		}
	}
}
.no-scroll {
	overflow: hidden;
}
.gradient-text {
	text-transform: uppercase;
	color: $main-color;
}
.arrow-to-top {
	position: fixed;
	display: block;
	opacity: 0;
	bottom: 20px;
	// 50
	right: 20px;
	height: 30px;
	width: 30px;
	padding: 1em;
	background-color: rgba(0, 0, 0, 0.5);
	border-radius: 14px;
	border: 2px solid transparent;
	box-sizing: content-box;
	transform: translateY(150%);
	cursor: pointer;
	pointer-events: none;
	transition: opacity 0.2s ease-in-out, transform 0.2s, border 0.2s ease-in-out,
		background-color 0.2s ease-in-out;
	z-index: 20130;
	&-active {
		transform: translateY(0);
		opacity: 1;
		pointer-events: auto;
	}
	& img {
		height: 100%;
		width: auto;
		transform: rotate(180deg);
	}
	&:hover {
		border: 2px solid $hover-color;
		background-color: $hover-color;
	}
}
@media (min-width: 576px) {
	.header {
		&__box {
		}
		&__buttons {
			flex-direction: row;

			width: 100%;
			&-button {
				&--second {
					width: max-content;
				}
			}
		}
	}
	.navbar {
		&__items {
			padding: 6em 8em;
		}
	}
	.container {
		padding: 0 8em;
	}
	.services {
		&__item {
			padding: 4em 6em;
		}
	}
	.process {
		&__item {
			gap: 6em;
			padding: 8em 6em;
		}
	}
}

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) {
	.header {
		height: 75dvh;
		&__buttons {
			gap: 5em;
		}
	}
	.services {
		grid-template-columns: repeat(2, 1fr);
		&__item {
			&-icon {
				height: 70px;
				width: 70px;
				padding: 2em;
			}
		}
	}
	.about-us {
		&__item {
			flex-direction: row;
			&:nth-child(2) {
				flex-direction: row-reverse;
			}
		}
		&__text,
		&__image {
			flex: 1;
		}
		&__text {
			justify-content: start;
			text-align: left;
			align-self: center;
			&-heading {
				margin: 0;
			}
		}
	}
	.process {
		grid-template-columns: repeat(2, 1fr);
		&__item {
			&::after {
				height: 80px;
				width: 80px;
				top: -20px;
				line-height: 80px;
				right: -20px;
			}
			& span {
				& img {
					height: 80px;
				}
			}
		}
	}
	.section {
		&-quote {
			padding: 20em 0;
		}
	}
	.contact {
		&__box {
			flex-direction: row;
			justify-content: space-between;
			max-width: 900px;
			margin: 5em auto;
		}
	}
	.arrow-to-top {
		height: 50px;
		width: 50px;
	}
}
@media (max-width: 991px) {
	.navbar__items {
		display: flex;
		position: absolute;
		flex-direction: column;
		justify-content: center;
		gap: 6em;
		height: calc(100dvh - 60px);
		width: 100%;
		background-color: $background-color;
		left: 0;
		right: 0;
		top: 60px;
		bottom: 0;
		transition: transform 0.6s;
		transform: translateX(100%);
	}
}
// Large devices (desktops, 992px and up)
@media (min-width: 992px) {
	.section {
		&-padding {
			padding: 12em 0;
		}
	}
	.header {
		background-image: url("./dist/img/header_big.jpg");
		&__box {
			margin-bottom: 10em;
		}
	}
	.navbar {
		&__content {
			height: 80px;
		}
		&__links {
			display: flex;
		}
		&__socialmedia {
			&-mobile {
				display: none;
			}
			&-desktop {
				display: flex;
				flex-direction: row;
			}
		}
		&__items {
			display: flex;
			gap: 4em;

			.navbar__link {
				font-size: 2rem;
			}
		}
		.burger {
			display: none;
		}
	}
	.services {
		grid-template-columns: repeat(3, 1fr);
		&__item {
		}
	}
	.process {
		grid-template-columns: repeat(3, 1fr);
		gap: 2em;
		&__item {
			padding: 10em 6em;
		}
	}
	.contact {
		&__box {
			margin: 10em auto 5em;
			&-email {
				& img {
					height: 100px;
				}
			}
		}
		&__socialmedia {
			&-item {
				& a {
					& img {
						height: 50px;
					}
				}
			}
		}
	}
	.section {
		&-contact {
			padding: 15em 0;
		}
	}
}

// X-Large devices (large desktops, 1200px and up)
@media (min-width: 1200px) {
	.process {
		gap: 6em;
	}
}

// XX-Large devices (larger desktops, 1400px and up)
