@use "../base/colors" as *;
@use "../base/mixins" as *;

.section-contact {
	background-image: url("../../../dist/img/contact_big.jpg");
	background-size: cover;
	background-position: center;
	position: relative;
	padding: 10em 0;
	z-index: 1;
	&::before {
		width: 100%;
		height: 100%;
		background: linear-gradient(
			180deg,
			rgba(68, 57, 168, 0) 41%,
			rgba(12, 12, 12, 1) 98%
		);
		z-index: -1;
		bottom: 0;
		left: 0;
	}
	&::after {
	}
	.section__box {
		&-text {
			color: #fff;
		}
	}
}

.contact {
	&__box {
		margin-top: 10em;
		display: flex;
		flex-direction: column;
		align-items: center;
		&-email {
			display: flex;
			align-items: center;
			gap: 4em;
            & img{
                height: 60px;
            }
			& a {
				color: #fff;
				font-size: clamp(2.4rem, 1.95rem + 1.25vw, 3.2rem);
                word-break: break-all;
			}
		}
	}
	&__socialmedia {
		display: flex;
		gap: 4em;
		margin: 5em 0;
		justify-content: space-evenly;
        &-item{
            & a{
                & img{
                    height: 40px;
                }
            }
        }
	}
}
