@use "../base/colors" as *;
@use "../abstracts/mixins" as *;

.section-faq {
	background-color: #fff;
	padding: 20em 0;
}

.accordion {
	margin: 10em auto;
	width: 100%;
	max-width: 900px;
	&__box {
		margin: 2em 0;
        border-bottom: 1px solid #949494;
		.plus {
			font-size: 5rem;
            font-weight: 300;

		}
		&-btn {
            font-weight: 400;
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: .5em 0em;
			width: 100%;
            border: none;
            background-color: #fff;
			color: #000;
			font-size: clamp(1.6rem, 1.375rem + 0.625vw, 2rem);
			text-align: left;
			border-radius: 20px;
			cursor: pointer;
            
		}
	}
	&__info {
        color: #282828;
		font-size: clamp(1.6rem, 1.375rem + 0.625vw, 2rem);
		transition: max-height 0.6s ease;
		pointer-events: none;
		max-height: 0;
		overflow: hidden;
		&.active {
			max-height: 350px;
		}
		&-text {
			padding: 1em 1em;
			font-size: clamp(1.6rem, 1.4875rem + 0.3125vw, 1.8rem);
		}
	}
}
