@use "../base/colors" as *;
.socialmedia__list {
	display: flex;
	gap: 2em;

	&-link {
	}
	&-icon {
		height: 35px;
		width: auto;
	}
}
.navbar {
	position: fixed;
	top: 0;
	z-index: 123123;
	width: 100%;
	transition: background-color 0.3s;
	&__link {
		color: #fff;
		font-size: clamp(2rem, 1.2135rem + 4.4944vw, 4rem);
	}
	&-shadow {
		background-color: hsl(0, 0%, 5%);
	}
	&__content {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 3em 0;
		width: 100%;
		height: 60px;
	}
	&__items {
		display: flex;
		gap: 4em;
		padding: 6em 4em;
		&.menu-active {
			display: flex;
			transform: translateX(0);
		}
	}
	&__logo {
		font-size: clamp(2.4rem, 2.175rem + 0.625vw, 2.8rem);
		color: #fff;
		font-weight: 600;
	}

	&__socialmedia {
		&-mobile {
			margin-top: auto;
			border-top: 1px solid #fff;
			padding: 5em 0;
			.socialmedia__list {
				flex-direction: row;
				gap: 4em;
				&-icon {
					height: 50px;
				}
			}
		}
		&-desktop {
			display: none;
			.socialmedia__list {
				&-item {
					&:nth-child(2) {
						border-left: 2px solid #8c8c8c;
						border-right: 2px solid #8c8c8c;
						padding: 0 2em;
					}
				}
			}
		}

		&-item {
			&:nth-child(2) {
				border-left: 1px solid #fff;
				border-right: 1px solid #fff;
				padding: 0 2em;
			}
		}
	}
}

.line {
	margin: 0.3em;
	height: 0.2em;
	background-color: #fff;
	border-radius: 20px;
	transition: background-color 0.3s, width 0.3s;
	&:nth-child(1) {
		width: 1.4em;
	}
	&:nth-child(2) {
		width: 3em;
	}
	&:nth-child(3) {
		width: 1.2em;
		margin-left: auto;
	}
}
.burger {
	display: flex;
	flex-direction: column;
	background: transparent;
	border: none;
	outline: none;
	cursor: pointer;
	&:focus {
		.line {
			width: 3em;
			background-color: $main-color;
		}
	}
}
