@use "../base/mixins" as *;
@use "../base/colors" as *;

.section-about-us {
	overflow: hidden;
	position: relative;
	z-index: 1;

	.section__box {
		&-text {
			color: #fff;
		}
	}
}

.about-us {
	margin-top: 12em;
	&__item {
		display: flex;
		justify-content: center;
		flex-direction: column;
		align-items: center;
		padding: 4em 0;
		max-width: 1100px;
		margin: 0 auto;
		opacity: 0;
		&:nth-child(1) {
			&.animate {
				@include fade-in-right;

				animation-delay: 0.1s;
			}
			.about-us__text {
				&-heading {
					border: 2px solid #d74343;
					color: #d74343;
				}
			}
		}

		&:nth-child(2) {
			opacity: 1;
			position: relative;
			z-index: 1;
			&::after {
				position: absolute;
				content: "";
				width: 100vw;
				height: 100%;
				background-color: #100f0f;
				z-index: -1;
			}
	
			.about-us__text {
				&-heading {
					border: 2px solid $main-color;
					color: $main-color;
				}
			}
		}
		&:nth-child(3) {
			&.animate {
				@include fade-in-left;

				animation-delay: 0.1s;
			}
			.about-us__text {
				&-heading {
					border: 2px solid #46d743;
					color: #46d743;
				}
			}
		}
	}

	&__text {
		// flex: 1;
		text-align: center;
		&-heading {
			margin: 0 auto;
			font-size: clamp(1.6rem, 1.2625rem + 0.9375vw, 2.2rem);
			border-radius: 12px;
			font-weight: 600;
			padding: 0.3em 0.6em;
			width: max-content;
		}
		&-text {
			font-size: clamp(2rem, 0.875rem + 3.125vw, 4rem);
			color: #fff;
			font-weight: 600;
			margin: 0.5em 0;
		}
		&-description {
			font-size: clamp(1.6rem, 1.2625rem + 0.9375vw, 2.2rem);
			color: #a8a8a8;
		}
	}

	&__image {
		// flex: 1;
		& img {
			height: 100%;
			width: 100%;
			object-fit: cover;
		}
	}
}
