@use "../base/colors" as *;
@use "../base/mixins" as *;

.header {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	height:80dvh;
	width: 100%;
	background-image: url("./dist/img/header_small.jpg");
	background-size: cover;
	background-position: center;
	&::after {
		@include background-shadow(0.3);
	}
	&::before {
		position: absolute;
		content: "";
		width: 100%;
		height: 100%;
        background: linear-gradient(180deg, rgba(68,57,168,0) 41%, rgba(12,12,12,1) 98%);
        z-index: -1;
	}
    z-index: 12;
	&__box {
		display: flex;
		flex-direction: column;
		align-items: center;
        gap: 6em;
		&-heading {
            text-align: center;
			font-weight: 500;
			font-size: clamp(3rem, 1.5375rem + 4.0625vw, 5.6rem);
			color: #fff;
		}
	}
	&__buttons {
		display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
		gap: 3em;
        width: 100%;
		&-button {
            width: 100%;
            max-width: 320px;
			padding: 0.6em 2em;
			border-radius: 8px;
			font-size: clamp(1.6rem, 1.15rem + 1.25vw, 2.4rem);
			font-weight: 500;
            text-align: center;
			&--first {
				background-color: $main-color;
                border: 2px solid $main-color;
				color: #fff;
                transition: color .3s, background-color .3s, border .3s;
                &:hover{
                    background-color: $hover-color;
                    border: 2px solid $hover-color;
                }
            }
			&--second {
                transition: color .3s, border .3s;
				border: 2px solid #fff;
				color: #fff;
                &:hover{
                    color: $main-color;
                    border: 2px solid $main-color;
                }
			}
		}
	}
}
