@use "../base/colors" as *;
@use "../abstracts/mixins" as *;
.footer{
    height: 80px;
    line-height: 80px;
    text-align: center;
    color: hsl(0, 0%, 55%);
    background-color: hsl(0, 3%, 8%);
    font-size: clamp(1.6rem, 1.375rem + 0.625vw, 2rem);
    border-top: 2px solid #3a3a3a;
    .gradient-text{
        font-weight: 600;
    }
    &-logo{
        color: #fff;
        font-weight: 600;
    }
}